var pop = require('popup');
var tplRight = require('../../../views/components/dailyWork-right.tpl');
var util = require('util');
var security = require("security");
var timeFormat = require("timeFormat");
var requestApi = appConfig.baseEnv;


//var tommorow = new Date() - 3600 * 1000 * 24;


var baogongDesListTpl = [
	'<table class="table-bordered table-striped" width="100%" align="center">',
		'<thead>',
			'<th >行号</th>',
			'<th >生产顺序号</th>',
			'<th >产品标识号</th>',
			'<th >车型描述</th>',
			'<th >报工数量</th>',
			'<th >报工时间</th>',
		'</thead>',
	'</table>'
].join('')

var baogongCtrl = {
	page : undefined,
	station:null,
	productNumList:[],
	"productAcquiItems": [],
    "routingAcquiItemAssigns":[],
    "tableList":['.plane-table-list','.plane-table-dailywork'],
    "queryParam" : {
    	siteCode:requestApi.api.siteCode,
        serviceCode : "GMES-CLIENT-PCS00017",
        languageCode : "zh",
        userCode:"",
        operationCode : "",
        stationCode : "",
        productNum : "",
        productTrackNum:"",
        minCreateDateTime : timeFormat.one(),
        maxCreateDateTime : ''
    },
	init() {
		var _this = this;
		security.isUniqueOperableStation(function(station){
			_this.station = station;
			_this.queryParam.stationCode = station.stationCode
			util.setBreadcrumb(['报工'+_this.station.stationCode]);
			_this.event();
			_this.loadPage();
			//_this.initPlanList();
		});
		
	},
	initPopup(data){
		var _this = this;
		var param = { 
		    selector:'.baogong',
		    tpl:tplRight(data),
		    submit:function(){
		    	_this.setProductAcquiItems()
			}
		};
		this.popup = pop(param);
	},
	event(){
		var _this = this;
		/*$(".open-client-popup").on("click",e => {
			_this.routingAcquiItemAssigns.length > 0 ? _this.popup.open() : _this.submitTable()
		});*/
		
		Rx.Observable
        .fromEvent($(".open-client-popup").get(0),'click')
        .throttleTime(2000)
        .subscribe(function(){
            _this.routingAcquiItemAssigns.length > 0 ? _this.popup.open() : _this.submitTable()
        });

		$('#productNum').keydown(e =>{
			if(e.keyCode === 13){
				_this.initTable(e.target.value)
				_this.setProductNum(e.target.value)
			}
		})
		$('.getData').on('click',function(e){
			var val = $('#productNum').val();
			_this.initTable(val)
			_this.setProductNum(val)
		})
		$('.tab-btn').on('click','a',function(e) {
			$(this).addClass('selected').siblings().removeClass('selected');
			var index = $(this).data('id');
			_this.tableList.forEach((item,i) =>{
				i === index ? $(item).removeClass('z-hide').addClass('z-show') : $(item).removeClass('z-show').addClass('z-hide')
			})
		})
	},
	initTable(proNum){
		var _this = this;
		var datas = {
			productNum:proNum,
			stationCode:_this.station.stationCode
		};
		util.service("GMES-CLIENT-PCS00015",datas,'',function(data){
			data.data.configEnums.length > 0 ? data.data.configEnums.length = 3 : data.data.hasConfigEnums = false;

			util.tplManager('baogongTplContent',data.data,'content-header');
			util.tplManager('baogongTplContentDesLeft',data.data,'content-left');
			_this.initPopup(data.data)
			_this.routingAcquiItemAssigns = data.data.routingAcquiItemAssigns;
			$('#productNum').attr('readonly','readonly');
		})

	
	},
	initPlanList(){
		//myApp.showPreloader("获取计划列表中...");
		var _this = this;
		util.service("GMES-CLIENT-PCS00021",{},'',function(data){
			//myApp.hidePreloader();
			data.data = data.data.splice(0,7);

			data.data = data.data.map(function(item){
				_this.productNumList.push(...item.productNumList)
				item.productNum = _this.abbreviation(_this.tranlate(item.productNumList,6));
				item.productTrackNum = _this.abbreviation(_this.tranlate(item.productTrackNumList,5));
				return item
			})
			$('#productNum').val(_this.productNumList[0])
			util.tplManager('baogongTplContentDesRight',data,'content-right .plane-table-list');
			var dom = document.createElement('div');
	        $('.baogong').on('mouseenter','.productNumList',function(e){
	            var $this = $(this);
	            dom.style.left = $this.offset().left+$this.width()/2+'px';
	            dom.style.top = $this.offset().top - $this.height()+'px';
	            dom.classList.add('num-detail-tip');
	            dom.innerHTML = $this.data('num');
	            $('.baogong').append(dom)
	        });
	        $('.baogong').on('mouseleave','.productNumList',function(e){
	            $('.num-detail-tip').remove()
	        });
		}).fail(()=>{
			//myApp.hidePreloader();
		})
	},
	loadPage : function(){
        var _this = this;
        this.datatableApi = util.dataTable($('.plane-table-dailywork table',this.page),{
            "scrollY": '300',
            "ordering": false,
            "order" : [],
            "pageLength":6,
            "ajax" : {
                serviceCode : "GMES-CLIENT-PCS00017",
                param : function(){
                    return  _this.queryParam;
                }
            },
            "columns": [
                {
                    "data": "id",
                    "render": function ( data, type, row,meta  ) {
                        return meta["settings"]["_iDisplayStart"] + meta.row+1
                    }
                },
                {
                    "data": "productionSeqNum",
                    "name": "productionSeqNum",
                },
                {
                    "data": "productNum",
                    "name": "productNum"
                },
                {
                    "data": "materialText",
                    "name": "materialText"
                },
                {
                    "data": "quantity",
                    "name": "quantity"
                },
                {
                    "data": "confirmDateTime",
                    "name": "confirmDateTime",
                    "render": function ( data, type, row ) {
                        return formatDate(data);
                        //将秒数换成日期格式 YY-MM-DD hh:mm:ss
                        function formatDate(time) {
                            var tmpDate=new Date(time);
                            var year = tmpDate.getFullYear();
                            var mathon = tmpDate.getMonth() + 1;
                            var day = tmpDate.getDate();
                            var hours = tmpDate.getHours();
                            var minutes = tmpDate.getMinutes();
                            var seconds = tmpDate.getSeconds();
                            return year + '-' + getzf(mathon) + '-' + getzf(day) + ' ' + getzf(hours) + ':' + getzf(minutes) + ':' +getzf(seconds);
                            //补0操作
                            function getzf(num){
                                if(parseInt(num) < 10){
                                    num = '0'+num;
                                }
                                return num;
                            }
                        }
                    },
                }
            ]
        })
	},
	abbreviation(arr = []){
		return arr.length > 2 ?  `${arr[0]}...${arr[arr.length-1]}` : arr;
	},
	tranlate(arr = [],lastindex = 0){
		return arr.map((item,i)=>{
			return item.substr(item.length-lastindex)
		})
	},
	setProductNum(val){
		this.ProductNum = val;
	},
	setProductAcquiItems(){
		var formData = myApp.formToJSON('#baogong-left-form');
		var data = {}
    	Object.keys(formData).forEach((item,i)=>{
    		var key = `productAcquiItems[${i}]`;
    		var keyNew1 = `${key}.acquiItemValue`;
    		var keyNew2 = `${key}.acquiItemCode`;
    		data[keyNew1] = formData[item];
    		data[keyNew2] = item;
    	})
    	this.submitTable(data)
	},
	clearDesList(){
		$('.plane-table-dailywork').html(baogongDesListTpl);
	},
	clearValue(selector){
		$(selector).find('.value').text('--')
	},
	submitTable(data = {}){
		//LB37724S6HX710563
		var _this = this;
		var datas = {
			productNum:$('#productNum').val(),
			stationCode:_this.station.stationCode,
			languageCode:'zh',
			userCode:security.getUserProfile().userCode,
			...data};

		/*util.service("GMES-CLIENT-PCS00016",datas,null).done(function(data){
			notie.alert({text: '完成', time: 3,type:1});
			_this.clearDesList()
			if($('#productNum').val() == _this.productNumList[0]){
				_this.productNumList.shift()
			}
			_this.clearValue('#content-header');
			$('#productNum').removeAttr('readonly');
			$('#productNum').val(_this.productNumList[0])
			_this.loadPage();
			//_this.initPlanList();
		})*/

		util.post("restful/service/execute",{...datas,serviceCode:"GMES-CLIENT-PCS00022"}).done(function(data){
			var { 
					errorMessage,
					returnCode
				} = data;
			if(returnCode == 2 || returnCode == 4){
				notie.force({text: errorMessage || '请求失败', time: 3,type:1});
				_this.clearValue('#content-header');
				$('#productNum').removeAttr('readonly').val('');
				return false
			}
			if(returnCode == 1 || returnCode == 3){
				notie.confirm({text: errorMessage || '请求失败',submitText:'继续报工',cancelText:'取消报工', 
				cancelCallback:function(){
					_this.clearValue('#content-header');
					$('#productNum').removeAttr('readonly').val('');
				},
				submitCallback:function(){
					util.service("GMES-CLIENT-PCS00016",datas,null).done(function(data){
						_this.clearDesList()
						if($('#productNum').val() == _this.productNumList[0]){
							_this.productNumList.shift()
						}
						_this.clearValue('#content-header');
						$('#productNum').removeAttr('readonly');
						$('#productNum').val(_this.productNumList[0])
						_this.loadPage();

					})
				}});
			}
			if(returnCode == 0){
				util.service("GMES-CLIENT-PCS00016",datas,null).done(function(data){
					notie.alert({text: '完成', time: 3,type:1});
					_this.clearDesList()
					if($('#productNum').val() == _this.productNumList[0]){
						_this.productNumList.shift()
					}
					_this.clearValue('#content-header');
					$('#productNum').removeAttr('readonly');
					$('#productNum').val(_this.productNumList[0])
					_this.loadPage();
					//_this.initPlanList();
				})
			}
		})
	}
};

module.exports = baogongCtrl



